export default {
  props: ["payOrder"],
  data() {
    return {
      count: 0,
      money: 0
    };
  },
  // localStorage
  created() {
    // this.active = JSON.parse(localStorage.getItem('active'))
  },
  methods: {},
  watch: {
    payOrder: {
      handler(newval) {
        // console.log(newval)
        this.count = newval.length;
        let totleprice = 0;
        for (var i = 0; i < newval.length; i++) {
          totleprice = totleprice + newval[i].num * newval[i].price;
        }
        this.money = totleprice;
      },
      deep: true,
      immediate: true
    }
  }
};